html {
  background-color: lightblue;
}
html .top-div {
  background-color: lightblue;
}
html .top-div .form-label {
  background-color: wheat;
  border-top-right-radius: 15px;
  border-top-left-radius: 15px;
  box-shadow: 0 0 10px black;
}
html .top-div .form-label i {
  position: relative;
  float: right;
  margin-top: 3px;
  margin-right: 10px;
}
html .top-div .form-label i:hover {
  cursor: pointer;
}
html .top-div a {
  color: black;
}
html .top-div #editor {
  resize: none;
  border-radius: 0;
  border-bottom-left-radius: 15px;
  box-shadow: 0 0 10px black;
}
html blockquote {
  margin-left: 1em;
  padding-left: 0.6em;
  border-left: 5px lightblue solid;
}
html #preview {
  padding: 1em;
  border-radius: 0;
  border-bottom-left-radius: 15px;
  box-shadow: 0 0 10px black;
}
html #preview img {
  width: 50%;
}/*# sourceMappingURL=App.css.map */