$background-color: lightblue;
$window-color: wheat;

html {
    background-color: $background-color;

    .top-div {
        background-color: $background-color;

        .form-label {
            background-color: $window-color;
            border-top-right-radius: 15px;
            border-top-left-radius: 15px;
            box-shadow: 0 0 10px black;

            i {
                position: relative;
                float: right;
                margin-top: 3px;
                margin-right: 10px;
            }

            i:hover {
                cursor: pointer;;
            }
        }

        a {
           color: black;
        }

        #editor {
            resize: none;
            border-radius: 0;
            border-bottom-left-radius: 15px;
            box-shadow: 0 0 10px black;
        }
    }

    blockquote {
        margin-left: 1em;
        padding-left: 0.6em;
        border-left: 5px $background-color solid;
    }

    #preview {
        padding: 1em;
        border-radius: 0;
        border-bottom-left-radius: 15px;
        box-shadow: 0 0 10px black;
        
        img {
            width: 50%;
        }
    }
}